<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>药物管理</BreadcrumbItem>
      <BreadcrumbItem>药物信息</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="药物编号">
            <Input v-model="searchForm.drugID" placeholder="请输入药物编号"></Input>
          </FormItem>
          <FormItem label="药物中文名称">
            <Input v-model="searchForm.drugCN" placeholder="请输入药物中文名称"></Input>
          </FormItem>
          <FormItem label="药物英文名称">
            <Input v-model="searchForm.drugEN" placeholder="请输入药物英文名称"></Input>
          </FormItem>
          <FormItem label="FDA药物状态">
            <Input v-model="searchForm.FDAStatus" placeholder="请输入FDA药物状态"></Input>
          </FormItem>
          <FormItem label="CFDA药物状态">
            <Input v-model="searchForm.CFDAStatus" placeholder="请输入CFDA药物状态"></Input>
          </FormItem>
          <FormItem label="药物类别">
            <Select v-model="searchForm.drugCategory" style="width:162px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="药物描述">
            <Input v-model="searchForm.drugDesc" placeholder="请输入药物描述"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" @click="addNewDrug" size="large" icon="android-add">添加新药物</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalData.length" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        searchForm:{
          drugID:'',
          drugCN:'',
          drugEN:'',
          FDAStatus:'',
          CFDAStatus:'',
          drugCategory:'',
          drugDesc:''
        },
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"药物编号",
            key:'drugID',
            align: 'center'
          },
          {
            title:"药物中文名称",
            key:'drugCN',
            align: 'center'
          },
          {
            title:"药物英文名称",
            key:'drugEN',
            align: 'center'
          },
          {
            title:"药物状态",
            key:'drugStatus',
            align: 'center'
          },
          {
            title:"药物类别",
            key:'drugCategory',
            align: 'center'
          },
          {
            title:"最后修改时间",
            key:'updateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.index)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            drugID:"11111",
            drugCN:"感冒灵",
            drugEN:"aaaaaaa",
            drugStatus:"你好",
            drugCategory:"感冒类",
            updateDate:'2018-02-02'
          },
          {
            drugID:"11111",
            drugCN:"感冒灵",
            drugEN:"aaaaaaa",
            drugStatus:"你好",
            drugCategory:"感冒类",
            updateDate:'2018-02-02'
          },
          {
            drugID:"11111",
            drugCN:"感冒灵",
            drugEN:"aaaaaaa",
            drugStatus:"你好",
            drugCategory:"感冒类",
            updateDate:'2018-02-02'
          },
          {
            drugID:"11111",
            drugCN:"感冒灵",
            drugEN:"aaaaaaa",
            drugStatus:"你好",
            drugCategory:"感冒类",
            updateDate:'2018-02-02'
          },
        ],
        totalData:[],
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('Success!');
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      addNewDrug(){
        this.$router.push("/basicdataModal/addnewdrug")
      },
      show (index) {
        console.log(index);
        this.$router.push("/basicdataModal/browdrugdetail")
      },
      edit (index) {
        console.log(index);
        this.$router.push("/basicdataModal/editdrugdetail")
      },
      remove(index){
        console.log(index);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      },
      handlePageSize(val){
        this.pageSize = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


