<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>转录本详情</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <span>Gene Symbol:11111</span>
        <span>基因编号:11111</span>
        <span>当前使用的转录本编号:11111</span>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewTranscript">添加新转录本</Button>
      </div>
      <div class="transcriptListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"转录本编号",
            key:'transcriptID',
            align: 'center'
          },
          {
            title:"转录本名称",
            key:'transcriptName',
            align: 'center'
          },
          {
            title:"是否是当前使用的转录本",
            key:'isUsed',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LastestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                // h('Button', {
                //   props: {
                //     type: 'text',
                //     size: 'large',
                //     icon: 'document'
                //   },
                //   style: {
                //     padding: '5px'
                //   },
                //   on: {
                //     click: () => {
                //       this.show(params.row.transcriptID)
                //     }
                //   }
                // }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.transcriptID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.transcriptID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            transcriptID:1111,
            transcriptName:"1111",
            isUsed:"yes",
            CreateDate:"2018-03-01",
            LastestUpdateDate:"2018-03-02"
          }
        ],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      addNewTranscript(){
        this.$router.push("/basicdataModal/addnewtranscript")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/browtranscriptdetail?transcriptid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/edittranscriptdetail?transcriptid=" + id)
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        // this.$http.post("/php/gene/",qs.stringify(obj)).then(response=>{
        //   console.log(response.data);
        //   this.tableData = response.data.data;
        //   this.totalNum = response.data.num;
        // })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        // this.$http.post("/php/gene/",qs.stringify(obj)).then(response=>{
        //   console.log(response.data);
        //   this.tableData = response.data.data;
        //   this.totalNum = response.data.num;
        // })
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    span{
      font-size:14px;
      color:@themeColor;
      margin: 0 20px;
    }
  }
  .addContainer{
    padding:10px;

  }
  .transcriptListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


