<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>文献管理</BreadcrumbItem>
      <BreadcrumbItem>作者信息</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="Last Name(姓)">
            <Input v-model="searchForm.lastName" placeholder="请输入作者"></Input>
          </FormItem>
          <FormItem label="Middle Name">
            <Input v-model="searchForm.middleName" placeholder="请输入年份"></Input>
          </FormItem>
          <FormItem label="First Name(名)">
            <Input v-model="searchForm.firstName" placeholder="请输入PubmedID"></Input>
          </FormItem>
          <FormItem label="创建日期">
            <DatePicker type="date" placeholder="请选择创建日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add">添加新作者</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalData.length" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        searchForm:{
          lastName:'',
          middleName:'',
          firstName:'',
          createDate:''
        },
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Last Name(姓)",
            key:'lastName',
            align: 'center'
          },
          {
            title:"Middle Name",
            key:'middleName',
            align: 'center'
          },
          {
            title:"First Name(名)",
            key:'firstName',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'createDate',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'updateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on:{
                    click:()=>{
                      this.edit(params.index)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            lastName:"感冒灵",
            middleName:"2017",
            firstName:'1111111',
            createDate:'lalalalala',
            updateDate:'111111111111'
          },
          {
            lastName:"感冒灵",
            middleName:"2017",
            firstName:'1111111',
            createDate:'lalalalala',
            updateDate:'111111111111'
          },
          {
            lastName:"感冒灵",
            middleName:"2017",
            firstName:'1111111',
            createDate:'lalalalala',
            updateDate:'111111111111'
          },
          {
            lastName:"感冒灵",
            middleName:"2017",
            firstName:'1111111',
            createDate:'lalalalala',
            updateDate:'111111111111'
          }
        ],
        totalData:[],
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        
      },
      addNewAuthor(){
        this.$router.push("/basicdataModal/addnewauthor")
      },
      show (index) {
        console.log(index);
        this.$router.push("/basicdataModal/browauthordetail")
      },
      edit (index) {
        console.log(index);
        this.$router.push("/basicdataModal/editauthordetail")        
      },
      remove(index){
        console.log(index);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      },
      handlePageSize(val){
        this.pageSize = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


