<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>靶向治疗</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="Molecular Profile">
            <Input v-model="searchForm.molProfile" placeholder="请输入Molecular Profile"></Input>
          </FormItem>
          <FormItem label="疾病信息">
            <Input v-model="searchForm.disease" placeholder="请输入疾病信息"></Input>
          </FormItem>
          <FormItem label="Response Type">
            <Select v-model="searchForm.resType" style="width:162px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai" disabled>London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="治疗信息">
            <Input v-model="searchForm.treat" placeholder="请输入治疗信息"></Input>
          </FormItem>
          <FormItem label="有效证据">
            <Input v-model="searchForm.evidence" placeholder="请输入有效证据"></Input>
          </FormItem>
          <FormItem label="证据类型">
            <Select v-model="searchForm.eviType" style="width:162px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai" disabled>London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
           <FormItem label="文献信息">
            <Input v-model="searchForm.reference" placeholder="请输入文献信息"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewTargetedTherapy">添加新靶向治疗</Button>
      </div>
      <div class="geneListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        searchForm:{
          molProfile:'',
          disease:'',
          resType:'',
          treat:'',
          evidence:'',
          eviType:'',
          reference:''
        },
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Molecular Profile",
            key:'molProfile',
            align: 'center'
          },
          {
            title:"疾病信息",
            key:'disease',
            align: 'center'
          },
          {
            title:"Response Type",
            key:'resType',
            align: 'center'
          },
          {
            title:"疗法信息",
            key:'treat',
            align: 'center'
          },
          {
            title:"证据描述",
            key:'evidence',
            align: 'center'
          },
          {
            title:"证据等级",
            key:'eviType',
            align: 'center'
          },
          {
            title:"文献信息",
            key:'reference',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.GeneID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.GeneID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            molProfile:'111',
            disease:'111',
            resType:'11111111',
            treat:'11111111',
            evidence:'11111111',
            eviType:'11111111',
            reference:'11111111'
          }
        ],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.pageSize = 10;
        this.pageNum = 1;
        let ChrID = this.searchForm.ChrName.substr(3,2)
        this.searchForm.ChrID = ChrID;
        let obj = {}
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
      },
      addNewTargetedTherapy(){
        this.$router.push("/targetedtherapymodal/addnewtargetedtherapy")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/browtargetedtherapydetail")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/edittargetedtherapydetail")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .geneListcontainer{
    .tableColumnDisplay{
      display:hidden;
    }
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
    
  }
  
}
</style>


