<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/drugclass">药物类别</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browdrugclassdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editdrugclassdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除此药物</Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="left" :label-width="100">
          <FormItem label="药物类别编号">
              <Input v-model="editFrom.drugClassID" :disabled="this.currentPath==='browdrugclassdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物类别名称（英文）">
            <Input v-model="editFrom.drugClassNameEN" :disabled="this.currentPath==='browdrugclassdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物类别名称（中文）">
              <Input v-model="editFrom.drugClassNameCN" :disabled="this.currentPath==='browdrugclassdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editFrom')" v-if="this.currentPath==='browdrugclassdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='editdrugclassdetail'">保存</Button>
            <Button @click="handleSubmit('editFrom')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editFrom:{
          drugClassID:'111',
          drugClassNameEN:'11',
          drugClassNameCN:'aaaa'
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editdrugclassdetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdataModal/drugclass')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


