<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/snpindel">SNP_InDel</BreadcrumbItem>
      <BreadcrumbItem>添加新的SNP_InDel变异</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的疾病路径</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的疾病路径</p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="120">
          <FormItem label="子疾病编号">
              <Input v-model="addForm.childDiseaseID" placeholder="请输入子疾病编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="子疾病名称（英文）">
            <Input v-model="addForm.childDiseaseNameEN" placeholder="请输入子疾病名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="子疾病名称（中文）">
            <Input v-model="addForm.childDiseaseNameCN" placeholder="请输入子疾病名称（中文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="父疾病编号">
              <Input v-model="addForm.diseaseID" placeholder="请输入父疾病编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="父疾病名称（英文）">
            <Input v-model="addForm.diseaseNameEN" placeholder="请输入父疾病名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="父疾病名称（中文）">
            <Input v-model="addForm.diseaseNameCN" placeholder="请输入父疾病名称（中文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          childDiseaseID:'',
          childDiseaseNameEN:'',
          childDiseaseNameCN:'',
          diseaseID:'',
          diseaseNameEN:'',
          diseaseNameCN:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push('/basicdataModal/diseasepath')
      },
      backList() {
        this.$router.push('/basicdataModal/diseasepath')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


