<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/cnv">CNV</BreadcrumbItem>
      <BreadcrumbItem>添加新的CNV变异</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的CNV变异</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的CNV变异</p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="100">
          <FormItem label="变异编号">
              <Input v-model="addForm.variantID" placeholder="请输入变异编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体编号">
            <Input v-model="addForm.chromoID" placeholder="请输入染色体编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="起始位置">
            <Input v-model="addForm.chromoPosition" placeholder="请输入起始位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="终止位置">
              <Input v-model="addForm.referenceGenomeSequence" placeholder="请输入终止位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异类型">
            <Select v-model="addForm.variantType" placeholder="请选择变异类型" style="width:280px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="addForm.clinicalMean" type="textarea" :rows="4" placeholder="请输入临床意义" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          variantID:'',
          chromoID:'',
          startPosition:'',
          endPosition:'',
          variantType:'',
          clinicalMean:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push('/basicdataModal/cnv')
      },
      backList() {
        this.$router.push('/basicdataModal/cnv')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


