<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/targetedtherapy">靶向治疗</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browtargetedtherapydetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='edittargetedtherapydetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" label-position="right" :label-width="120">
          <FormItem label="创建时间">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="editForm.updateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Molecular Profile">
            <Input v-model="editForm.molProfile" :disabled="currentPath==='browtargetedtherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病信息">
            <Input v-model="editForm.disease" :disabled="currentPath==='browtargetedtherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Response Type">
            <Select v-model="editForm.resType" :disabled="currentPath==='browtargetedtherapydetail'" style="width:280px">
              <Option value="11">New York</Option>
              <Option value="22" disabled>London</Option>
              <Option value="33">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="治疗信息">
            <Input v-model="editForm.treat" :disabled="currentPath==='browtargetedtherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证据描述">
            <Input v-model="editForm.evidence" :disabled="currentPath==='browtargetedtherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证据等级">
            <Select v-model="editForm.eviType" :disabled="currentPath==='browtargetedtherapydetail'" style="width:280px">
              <Option value="1111">New York</Option>
              <Option value="2222">London</Option>
              <Option value="3333">Sydney</Option>
            </Select>
          </FormItem>
           <FormItem label="文献信息">
            <Input v-model="editForm.reference" :disabled="currentPath==='browtargetedtherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="英文功能描述">
            <Input v-model="editForm.Synonyms" :disabled="currentPath==='browtargetedtherapydetail'" type="textarea" :rows="4" style="width: 280px"></Input>
            <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit" v-if="currentPath==='browtargetedtherapydetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='edittargetedtherapydetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        ChemoID:location.search.split("=")[1],
        editForm:{
          molProfile:'111',
          disease:'111',
          resType:'11',
          treat:'1111111',
          evidence:'11',
          eviType:'1111',
          reference:'111',
          Synonyms:'111',
          CreateDate:'111',
          updateDate:'111'
        }
      }
    },
    methods: {
      editSubmit(){
        this.currentPath = 'edittargetedtherapydetail'
      },
      handleSubmit(name) {
        this.$router.push("/targetedtherapymodal/targetedtherapy")
      },
      backList(){
        this.$router.push("/targetedtherapymodal/targetedtherapy")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


