<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/variantprofile">突变谱</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browVariantProfiledetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='editVariantProfiledetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" label-position="right" :label-width="120">
          <FormItem label="创建时间">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="editForm.updateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="VariantProfile名称">
            <Input v-model="editForm.varProName" :disabled="currentPath==='browVariantProfiledetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="VariantProfile描述">
            <Input v-model="editForm.varProDesc"  :disabled="currentPath==='browVariantProfiledetail'" type="textarea" :rows="4" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit" v-if="currentPath==='browVariantProfiledetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='editVariantProfiledetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        ChemoID:location.search.split("=")[1],
        editForm:{
          varProName:'111',
          varProDesc:'111',
          CreateDate:'111',
          updateDate:'111'
        }
      }
    },
    methods: {
      editSubmit(){
        this.currentPath = 'editVariantProfiledetail'
      },
      handleSubmit(name) {
        this.$router.push("/targetedtherapymodal/variantprofile")
      },
      backList(){
        this.$router.push("/targetedtherapymodal/variantprofile")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


