<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/clinicaltrial">临床试验</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="browContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="100">
          <FormItem label="创建时间">
            <Input v-model="browForm.CreateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="browForm.updateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验编号">
              <Input v-model="browForm.cliID" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验名称">
              <Input v-model="browForm.cliName" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验分期">
            <Input v-model="browForm.cliPeriod" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验状态">
              <Input v-model="browForm.cliStatus" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit">编辑</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
      <div class="relatedContainer">
        <div class="deleteContainer">
          <Button type="warning" style="width: 120px">添加新靶向治疗</Button>
        </div>
        <Table stripe border ref="selection" height="200"  :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        cliID:location.search.split("=")[1],
        browForm:{
          cliID:'11',
          cliName:'11',
          cliPeriod:'11',
          cliStatus:'11',
          CreateDate:'111',
          updateDate:'111'
        },
        multipleSelection:[],
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Molecular Profile",
            key:'molProfile',
            align: 'center'
          },
          {
            title:"疾病信息",
            key:'disease',
            align: 'center'
          },
          {
            title:"Response Type",
            key:'resType',
            align: 'center'
          },
          {
            title:"疗法信息",
            key:'treat',
            align: 'center'
          },
          {
            title:"证据描述",
            key:'evidence',
            align: 'center'
          },
          {
            title:"证据等级",
            key:'eviType',
            align: 'center'
          },
          {
            title:"文献信息",
            key:'reference',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.GeneID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.GeneID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            molProfile:'111',
            disease:'111',
            resType:'11111111',
            treat:'11111111',
            evidence:'11111111',
            eviType:'11111111',
            reference:'11111111'
          }
        ],
      }
    },
    methods: {
      editSubmit() {
        this.$router.push('/targetedtherapymodal/editclinicaltrialdetail?cliid=' + this.cliID)
      },
      backList(){
        this.$router.push('/targetedtherapymodal/clinicaltrial')
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .browContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relatedContainer{
    border-top:1px dotted #ccc;
    .batchContainer{
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
  }
}
</style>


