<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>转录本详情</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browtranscriptdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='edittranscriptdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="left" :label-width="120">
          <FormItem label="Gene Symbol">
              <Input v-model="editFrom.Symbol" disabled placeholder="请选择Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因名称">
              <Input v-model="editFrom.GeneName" disabled placeholder="请输入基因名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本编号">
            <Input v-model="editFrom.TranscriptID" disabled placeholder="请输入转录本编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本名称">
              <Input v-model="editFrom.TranscriptName" :disabled="this.currentPath==='browtranscriptdetail'" placeholder="请输入转录本名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="是否设置为当前使用的转录本" prop="isCurrentTrans">
            <RadioGroup v-model="editFrom.isCurrentTrans">
              <Radio label="是" value="是"></Radio>
              <Radio label="否" value="否"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editFrom')" v-if="this.currentPath==='browtranscriptdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='edittranscriptdetail'">保存</Button>
            <Button @click="handleSubmit('editFrom')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        transcriptID:location.search.split("=")[1],
        editFrom:{
          Symbol:'111',
          GeneName:'aaa',
          TranscriptID:'111',
          TranscriptName:'吗丁啉',
          isCurrentTrans:'是'
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'edittranscriptdetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdataModal/transcriptlist')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


