<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/author">作者信息</BreadcrumbItem>
      <BreadcrumbItem>添加新的作者</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的作者</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的作者</p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="100">
          <FormItem label="First Name（姓）">
              <Input v-model="addForm.firstName" placeholder="请输入First Name" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Middle Name">
            <Input v-model="addForm.middleName" placeholder="请输入Middle Name" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Last Name（名）">
              <Input v-model="addForm.lastName" placeholder="请输入Last Name" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="handleSubmit('addForm')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          firstName:'',
          middleName:'',
          lastName:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push('/basicdataModal/author')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


