<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/author">作者信息</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browauthordetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editauthordetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除此作者</Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="left" :label-width="100">
          <FormItem label="First Name（姓）">
              <Input v-model="editFrom.firstName" :disabled="this.currentPath==='browauthordetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Middle Name">
            <Input v-model="editFrom.middleName" :disabled="this.currentPath==='browauthordetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Last Name（名）">
              <Input v-model="editFrom.lastName" :disabled="this.currentPath==='browauthordetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editFrom')" v-if="this.currentPath==='browauthordetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='editauthordetail'">保存</Button>
            <Button @click="handleSubmit('editFrom')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editFrom:{
          firstName:'111',
          middleName:'11111111',
          lastName:'1111111111'
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editauthordetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdataModal/author')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


