<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/cnv">CNV</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browfusiondetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editfusiondetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="editFrom.createDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后修改日期">
            <Input v-model="editFrom.updateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异编号">
            <Input v-model="editFrom.variantID" :disabled="this.currentPath==='browfusiondetail'" style="width: 280px"></Input>
          </FormItem>
          <div style="overflow:hidden">
            <div class="sortContainer">
              <p>5' Partner Gene</p>
              <FormItem label="Gene Symbol">
                <Input v-model="editFrom.fiveGeneSymbol" :disabled="this.currentPath==='browfusiondetail'" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="Last Oberved Exon">
                <Input v-model="editFrom.lastObervedExon" :disabled="this.currentPath==='browfusiondetail'" placeholder="请输入Last Oberved Exon" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="Infered Breakpoint">
                  <Input v-model="editFrom.fiveInferedBreakpoint" :disabled="this.currentPath==='browfusiondetail'" placeholder="请输入Infered Breakpoint" style="width: 280px"></Input>
              </FormItem>
            </div>
            <div class="sortContainer">
              <p>3' Partner Gene</p>
              <FormItem label="Gene Symbol">
                <Input v-model="editFrom.threeGeneSymbol" :disabled="this.currentPath==='browfusiondetail'" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="First Oberved Exon">
                <Input v-model="editFrom.firstObervedExon" :disabled="this.currentPath==='browfusiondetail'" placeholder="请输入First Oberved Exon" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="Infered Breakpoint">
                  <Input v-model="editFrom.threeInferedBreakpoint" :disabled="this.currentPath==='browfusiondetail'" placeholder="请输入Infered Breakpoint" style="width: 280px"></Input>
              </FormItem>
            </div>
          </div>
          <FormItem label="临床意义">
            <Input v-model="editFrom.clinicalMean" :disabled="this.currentPath==='browfusiondetail'" type="textarea" :rows="4" style="width: 530px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit('editFrom')" v-if="this.currentPath==='browfusiondetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='editfusiondetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editFrom:{
          createDate:'2017-01-01',
          updateDate:'2017-01-01',
          variantID:111,
          variantID:'',
          fiveGeneSymbol:'',
          lastObervedExon:'',
          fiveInferedBreakpoint:'',
          threeGeneSymbol:'',
          firstObervedExon:'',
          threeInferedBreakpoint:'',
          clinicalMean:'1111'
        },
      }
    },
    methods: {
      editSubmit(){
        this.currentPath = 'editfusiondetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdatamodal/fusion')
      },
      backList() {
        this.$router.push('/basicdatamodal/fusion')
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .sortContainer{
      float:left;
      padding: 25px 10px 0;
      background:@bgc;
      margin:0 15px 25px;
      margin-top:0;
      border:1px solid @bgc;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


