<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/cnv">CNV</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browcnvdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editcnvdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="editFrom.createDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后修改日期">
            <Input v-model="editFrom.updateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异编号">
            <Input v-model="editFrom.variantID" :disabled="this.currentPath==='browcnvdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体编号">
            <Input v-model="editFrom.chromoID" :disabled="this.currentPath==='browcnvdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="起始位置">
            <Input v-model="editFrom.startPosition" :disabled="this.currentPath==='browcnvdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="终止位置">
            <Input v-model="editFrom.endPosition" :disabled="this.currentPath==='browcnvdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="editFrom.clinicalMean" :disabled="this.currentPath==='browcnvdetail'" type="textarea" :rows="4" style="width: 530px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit('editFrom')" v-if="this.currentPath==='browcnvdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='editcnvdetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editFrom:{
          createDate:'2017-01-01',
          updateDate:'2017-01-01',
          variantID:111,
          chromoID:111,
          startPosition:'11111',
          endPosition:'11111',
          clinicalMean:'1111111111'
        },
      }
    },
    methods: {
      editSubmit(){
        this.currentPath = 'editcnvdetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdatamodal/cnv')
      },
      backList() {
        this.$router.push('/basicdatamodal/cnv')
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


