<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/clinicaltrial">临床试验</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" label-position="right" :label-width="120">
          <!--<FormItem label="创建时间">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="editForm.updateDate" disabled style="width: 280px"></Input>
          </FormItem>-->
          <FormItem label="临床试验编号">
              <Input v-model="editForm.cliID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验名称">
              <Input v-model="editForm.cliName" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验分期">
            <Input v-model="editForm.cliPeriod" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验状态">
              <Input v-model="editForm.cliStatus" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        cliID:location.search.split("=")[1],
        editForm:{
          cliID:'11',
          cliName:'11',
          cliPeriod:'11',
          cliStatus:'11',
          // CreateDate:'111',
          // updateDate:'111'
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push("/targetedtherapymodal/clinicaltrial")
      },
      backList(){
        this.$router.push("/targetedtherapymodal/clinicaltrial")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


