<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/snpindel">SNP_InDel</BreadcrumbItem>
      <BreadcrumbItem>添加新的SNP_InDel变异</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的SNP_InDel变异</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的SNP_InDel变异</p>
        <Form :inline="true" ref="addForm" :model="addForm" :rules='rule' label-position="right" :label-width="120">
          <FormItem label="染色体名称" prop="ChrName">
            <Input v-model="addForm.ChrName" autofocus placeholder="请输入染色体名称" autofocus style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体位置">
            <Input v-model="addForm.Position" placeholder="请输入染色体位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="参考基因组序列">
              <Input v-model="addForm.REF_base" placeholder="请输入参考基因组序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="addForm.ALT_base" placeholder="请输入ALT序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <!--<Input v-model="addForm.Symbol" placeholder="输入首字母自动提供多条匹配信息" style="width: 280px"></Input>-->
            <AutoComplete v-model="addForm.Symbol" :data="geneSybolData" placeholder="请输入Gene Symbol" style="width:280px">
            </AutoComplete>
          </FormItem>
          <FormItem label="HGVSp">
            <Input v-model="addForm.HGVSp" placeholder="请输入HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="addForm.HGVSc" placeholder="请输入HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Impact">
            <Input v-model="addForm.Impact" placeholder="请输入Impact" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="addForm.Clin_Sig" placeholder="请输入临床意义" style="width: 695px"></Input>
          </FormItem>
          <div>
            <FormItem label="英文功能描述">
              <Input v-model="addForm.functionDescEN" type="textarea" :rows="4" placeholder="请输入英文功能描述" style="width: 530px"></Input>
              <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
            </FormItem>
          </div>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
import { sendChrID, reciveChrID } from '../../../../assets/switchChrName2ID.js'
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post('/php/gsymbol/').then(response=>{
        console.log(response.data);
        this.allSymbol = response.data;
        // this.geneSybolData = this.allSymbol
      })
    },
    data() {
      return {
        allSymbol:[],
        geneSybolData:[1,2,3],
        addForm: {
          ChrName:'',
          Position:'',
          REF_base:'',
          ALT_base:'',
          Symbol:'',
          HGVSp:'',
          HGVSc:'',
          Impact:'',
          Clin_Sig:'',
          functionDescEN:''
        },
        rule:{
          ChrName:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur'
            }
          ],
          Symbol:[
            {
              required: true, message: 'Gene Symbol 不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      // focus1(){
      //   this.geneSybolData = this.allSymbol.filter(item=>{
      //     if(item.index<6)
      //   })
      // },
      handleSubmit(name) {
        let obj = this.addForm;
        obj.ChrID = sendChrID(obj.ChrName)
        // console.log(obj.ChrID);
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(obj.ChrID!='error'){
              this.$http.post("/php/vrsnpindel/add.php",qs.stringify(obj)).then(response=>{
                // console.log(response.data);
                if(response.data===0){
                  this.$Message.error('请检查Gene Symbol的输入内容');
                }else{
                  if(response.data){
                    this.$Message.success('添加成功，即将跳转页面...');
                    setTimeout(()=>{
                      this.$router.push('/basicdataModal/snpindel')   
                    },1000)
                  }else{
                    this.$Message.error('添加失败，请检查输入的内容');
                  }
                }
              })
            }else{
              this.$Message.error('染色体名称错误，请重新输入');
            }
            
          }
        })
        
      },
      backList() {
        this.$router.push('/basicdataModal/snpindel')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


