<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/targetedtherapy">靶向治疗</BreadcrumbItem>
      <BreadcrumbItem>添加新的靶向治疗</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的靶向治疗</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的靶向治疗</p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="120">
          <FormItem label="Molecular Profile">
            <Input v-model="addForm.molProfile" placeholder="请输入Molecular Profile" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病信息">
            <Input v-model="addForm.disease" placeholder="请输入疾病信息" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Response Type">
            <Select v-model="addForm.resType" style="width:280px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai" disabled>London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="治疗信息">
            <Input v-model="addForm.treat" placeholder="请输入治疗信息" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证据描述">
            <Input v-model="addForm.evidence" placeholder="请输入有效证据" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证据等级">
            <Select v-model="addForm.eviType" style="width:280px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
           <FormItem label="文献信息">
            <Input v-model="addForm.reference" placeholder="请输入文献信息" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="英文功能描述">
            <Input v-model="addForm.Synonyms" type="textarea" :rows="4" placeholder="请输入英文功能描述" style="width: 280px"></Input>
            <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          molProfile:'',
          disease:'',
          resType:'',
          treat:'',
          evidence:'',
          eviType:'',
          reference:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        
      },
      backList(){
        this.$router.push("/targetedtherapymodal/targetedtherapy")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


