<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/diseasepath">疾病路径</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browdiseasepathdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editdiseasepathdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除此路径</Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editFrom.createDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="子疾病编号">
              <Input v-model="editFrom.childDiseaseID" :disabled="this.currentPath==='browdiseasepathdetail'" placeholder="请输入子疾病编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="子疾病名称（英文）">
            <Input v-model="editFrom.childDiseaseNameEN" :disabled="this.currentPath==='browdiseasepathdetail'" placeholder="请输入子疾病名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="子疾病名称（中文）">
            <Input v-model="editFrom.childDiseaseNameCN" :disabled="this.currentPath==='browdiseasepathdetail'" placeholder="请输入子疾病名称（中文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="父疾病编号">
              <Input v-model="editFrom.diseaseID" :disabled="this.currentPath==='browdiseasepathdetail'" placeholder="请输入父疾病编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="父疾病名称（英文）">
            <Input v-model="editFrom.diseaseNameEN" :disabled="this.currentPath==='browdiseasepathdetail'" placeholder="请输入父疾病名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="父疾病名称（中文）">
            <Input v-model="editFrom.diseaseNameCN" :disabled="this.currentPath==='browdiseasepathdetail'" placeholder="请输入父疾病名称（中文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后修改日期">
              <Input v-model="editFrom.updateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editFrom')" v-if="this.currentPath==='browdiseasepathdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='editdiseasepathdetail'">保存</Button>
            <Button @click="backList()">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editFrom:{
          createDate:'2017-01-01',
          childDiseaseID:'11111',
          childDiseaseNameEN:'111',
          childDiseaseNameCN:'1111',
          diseaseID:'111',
          diseaseNameEN:'111',
          diseaseNameCN:'111',
          updateDate:'2017-12-01'
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editdiseasepathdetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdataModal/diseasepath')
      },
      backList() {
        this.$router.push('/basicdataModal/diseasepath')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


