<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>基因管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="变异信息">
            <Input v-model="searchForm.VariantName" placeholder="请输入变异名称"></Input>
          </FormItem>
          <FormItem label="基因型">
            <Input v-model="searchForm.hgvsp" placeholder="请输入基因型"></Input>
          </FormItem>
          <FormItem label="疾病名称">
            <Input v-model="searchForm.disName" placeholder="请输入疾病名称"></Input>
          </FormItem>
          <FormItem label="药物名称">
            <Input v-model="searchForm.drugName" placeholder="请输入药物名称"></Input>
          </FormItem>
          <FormItem label="描述信息">
            <Input v-model="searchForm.desc" placeholder="请输入描述信息"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewGene">添加新基因型、药物和疾病信息</Button>
      </div>
      <div class="geneListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <template>
          <div></div>
        </template>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
import expandTpl from './expandTpl.vue'
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    components:{expandTpl},
    data() {
      return {
        searchForm:{
          hgvsp:'',
          VariantName:'',
          disName:'',
          drugName:'',
          desc:''
        },
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"描述信息",
            type: 'expand',
            align: 'center',
            width: 80,
            render: (h, params) => {
              return h(expandTpl, {
                props: {
                  row: params.row
                }
              })
            }
          },
          {
            title:"变异信息",
            key:'VariantName',
            align: 'center',
            width: 100,
          },
          {
            title:"基因型",
            key:'hgvsp',
            align: 'center'
          },
          {
            title:"疾病名称",
            key:'DisName',
            align: 'center'
          },
          {
            title:"药物名称",
            key:'DrugName',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.GeneID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.GeneID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            VariantName:'111',
            hgvsp:'111',
            DisName:'11111111',
            DrugName:'11111111',
            LatestUpdateDate:'111111111',
            desc:"hello"
          }
        ],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.pageSize = 10;
        this.pageNum = 1;
        let ChrID = this.searchForm.ChrName.substr(3,2)
        this.searchForm.ChrID = ChrID;
        let obj = {}
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
      },
      addNewGene(){
        this.$router.push("/chemotherapymodal/addnewchemotherapy")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/chemotherapymodal/browchemotherapydetail")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/chemotherapymodal/editchemotherapydetail")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .geneListcontainer{
    .tableColumnDisplay{
      display:hidden;
    }
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
    
  }
  
}
</style>


