function sendChrID(chrname){
  if (chrname.substr(0, 3).trim() === 'chr' && chrname.trim().length > 3) {
    let chrid = chrname.substr(3, 2)
    if (Number(chrid) < 23 && Number(chrid) > 0) {
      return chrid
    } else if (chrid === '23') {
      return 'X'
    } else if (chrid === '24') {
      return 'Y'
    } else if (chrid === '25') {
      return 'M'
    } else {
      return 'error'
    }
  } else {
    return 'error'
  }
}
function reciveChrID(chrid){
  if (Number(chrid) < 23 && Number(chrid) > 0) {
    return 'chr' + chrid
  } else if (Number(chrid) === 23) {
    return 'chrX'
  } else if (Number(chrid) === 24) {
    return 'chrY'
  } else if (Number(chrid) === 25) {
    return 'chrM'
  } else {
    return 'error'
  }
}
export { sendChrID, reciveChrID }


