<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/chemotherapymodal/chemotherapy">化学药物治疗</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browchemotherapydetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='editchemotherapydetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" label-position="right" :label-width="100">
          <FormItem label="变异位点">
            <Input v-model="editForm.varPosition" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因型">
              <Input v-model="editForm.hgvsp" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病编号">
            <Input v-model="editForm.disID" :disabled="this.currentPath==='browchemotherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病名称">
              <Input v-model="editForm.disName" :disabled="this.currentPath==='browchemotherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物编号">
            <Input v-model="editForm.drugID" :disabled="this.currentPath==='browchemotherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物名称">
              <Input v-model="editForm.drugName" :disabled="this.currentPath==='browchemotherapydetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="英文功能描述">
              <Input v-model="editForm.Synonyms" type="textarea" :rows="4" style="width: 280px" :disabled="this.currentPath==='browchemotherapydetail'"></Input>
              <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit" v-if="currentPath==='browchemotherapydetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='editchemotherapydetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        ChemoID:location.search.split("=")[1],
        editForm:{
          varPosition:'111',
          hgvsp:'111',
          disID:'111',
          disName:'111',
          drugID:'111',
          drugName:'111',
          Synonyms:'11'
        }
      }
    },
    methods: {
      editSubmit(){
        this.currentPath = 'editchemotherapydetail'
      },
      handleSubmit(name) {
        this.$router.push('/chemotherapymodal/chemotherapy')
      },
      backList(){
        this.$router.push("/chemotherapymodal/chemotherapy")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


