<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>临床试验</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="临床试验编号">
            <Input v-model="searchForm.cliID" placeholder="请输入临床试验编号"></Input>
          </FormItem>
          <FormItem label="临床试验名称">
            <Input v-model="searchForm.cliName" placeholder="请输入临床试验名称"></Input>
          </FormItem>
          <FormItem label="临床试验分期">
            <Input v-model="searchForm.cliPeriod" placeholder="请输入临床试验状态"></Input>
          </FormItem>
          <FormItem label="临床试验状态">
            <Input v-model="searchForm.cliStatus" placeholder="请输入临床试验状态"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewClinicalTrial">添加新临床试验</Button>
      </div>
      <div class="geneListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        searchForm:{
          cliID:'',
          cliName:'',
          cliPeriod:'',
          cliStatus:''
        },
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"临床试验编号",
            key:'cliID',
            align: 'center'
          },
          {
            title:"临床试验名称",
            key:'cliName',
            align: 'center'
          },
          {
            title:"临床试验分期",
            key:'cliPeriod',
            align: 'center'
          },
          {
            title:"临床试验状态",
            key:'cliStatus',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.GeneID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.GeneID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            cliID:'111',
            cliName:'111',
            cliPeriod:'11111111',
            cliStatus:'11111111'
          }
        ],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.pageSize = 10;
        this.pageNum = 1;
        let ChrID = this.searchForm.ChrName.substr(3,2)
        this.searchForm.ChrID = ChrID;
        let obj = {}
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
      },
      addNewClinicalTrial(){
        this.$router.push("/targetedtherapymodal/addnewClinicalTrial")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/browclinicaltrialdetail")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/editclinicaltrialdetail")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        let obj = this.searchForm;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .geneListcontainer{
    .tableColumnDisplay{
      display:hidden;
    }
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
    
  }
  
}
</style>


