<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>变异管理</BreadcrumbItem>
      <BreadcrumbItem>FUSION</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="Gene Symbol">
            <Input v-model="searchForm.geneSymbol" placeholder="请输入Gene Symbol"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="searchForm.clinicalMean" placeholder="请输入临床意义"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" @click="addNewFusion" size="large" icon="android-add">添加新FUSION</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalData.length" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        searchForm:{
          geneSymbol:'',
          clinicalMean:''
        },
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"变异编号",
            key:'variantID',
            align: 'center'
          },
          {
            title:"5‘ Partner Gene Symbol",
            key:'fivePartnerGeneSymbol',
            align: 'center'
          },
          {
            title:"5‘ Partner Last Observed Exon",
            key:'fivePartnerLastObservedExon',
            align: 'center'
          },
          {
            title:"5‘ Partner Inferred Breakpoint",
            key:'fivePartnerInferredBreakpoint',
            align: 'center'
          },
          {
            title:"3‘ Partner Gene Symbol",
            key:'threePartnerGeneSymbol',
            align: 'center'
          },
          {
            title:"3‘ Partner First Observed Exon",
            key:'threePartnerFirstObservedExon',
            align: 'center'
          },
          {
            title:"3‘ Partner Inferred Breakpoint",
            key:'threePartnerInferredBreakpoint',
            align: 'center'
          },
          {
            title:"临床意义",
            key:'clinicalMean',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on:{
                    click:()=>{
                      this.edit(params.index)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            variantID:"11111",
            fivePartnerGeneSymbol:"11111",
            fivePartnerLastObservedExon:"11111111",
            fivePartnerInferredBreakpoint:"11111111111111",
            threePartnerGeneSymbol:"11111111111111",
            threePartnerFirstObservedExon:'11111111',
            threePartnerInferredBreakpoint:'11111111',
            clinicalMean:'11111111111111'
          },
          {
            variantID:"11111",
            fivePartnerGeneSymbol:"11111",
            fivePartnerLastObservedExon:"11111111",
            fivePartnerInferredBreakpoint:"11111111111111",
            threePartnerGeneSymbol:"11111111111111",
            threePartnerFirstObservedExon:'11111111',
            threePartnerInferredBreakpoint:'11111111',
            clinicalMean:'11111111111111'
          },
          {
            variantID:"11111",
            fivePartnerGeneSymbol:"11111",
            fivePartnerLastObservedExon:"11111111",
            fivePartnerInferredBreakpoint:"11111111111111",
            threePartnerGeneSymbol:"11111111111111",
            threePartnerFirstObservedExon:'11111111',
            threePartnerInferredBreakpoint:'11111111',
            clinicalMean:'11111111111111'
          },
          {
            variantID:"11111",
            fivePartnerGeneSymbol:"11111",
            fivePartnerLastObservedExon:"11111111",
            fivePartnerInferredBreakpoint:"11111111111111",
            threePartnerGeneSymbol:"11111111111111",
            threePartnerFirstObservedExon:'11111111',
            threePartnerInferredBreakpoint:'11111111',
            clinicalMean:'11111111111111'
          },
        ],
        totalData:[],
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('Success!');
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      addNewFusion(){
        this.$router.push("/basicdataModal/addnewfusion")
      },
      show (index) {
        // console.log(index);
        this.$router.push("/basicdatamodal/browfusiondetail")
      },
      edit (index) {
        // console.log(index);
        this.$router.push("/basicdatamodal/editfusiondetail")
      },
      remove(index){
        console.log(index);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      },
      handlePageSize(val){
        this.pageSize = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


