import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Dashboard from '@/components/Dashboard'
import BasicdataModal from '@/components/BasicdataModal'
import ChemoTherapyModal from '@/components/ChemoTherapyModal'
import TargetedTherapyModal from '@/components/TargetedTherapyModal'

import Chromo from '@/components/basic/chromo/Chromo'
import AddNewChromo from '@/components/basic/chromo/AddNewChromo'
import EditChromoDetail from '@/components/basic/chromo/EditChromoDetail'

import Gene from '@/components/basic/gene/Gene'
import AddNewGene from '@/components/basic/gene/AddNewGene'
import EditGeneDetail from '@/components/basic/gene/EditGeneDetail'
import BrowGeneDetail from '@/components/basic/gene/BrowGeneDetail'
import TranscriptList from '@/components/basic/gene/TranscriptList'
import AddNewTranscript from '@/components/basic/gene/AddNewTranscript'
import EditTranscriptDetail from '@/components/basic/gene/EditTranscriptDetail'

import SnpIndel from '@/components/basic/variantManage/SNP_InDel/SnpIndel'
import AddNewSNPIndel from '@/components/basic/variantManage/SNP_InDel/AddNewSNPIndel'
import BrowSNPIndelDetail from '@/components/basic/variantManage/SNP_InDel/BrowSNPIndelDetail'
import EditSNPIndelDetail from '@/components/basic/variantManage/SNP_InDel/EditSNPIndelDetail'

import CNV from '@/components/basic/variantManage/CNV/CNV'
import AddNewCNV from '@/components/basic/variantManage/CNV/AddNewCNV'
import EditCNVDetail from '@/components/basic/variantManage/CNV/EditCNVDetail'

import FUSION from '@/components/basic/variantManage/FUSION/FUSION'
import AddNewFusion from '@/components/basic/variantManage/FUSION/AddNewFusion'
import EditFusionDetail from '@/components/basic/variantManage/FUSION/EditFusionDetail'

import Disease from '@/components/basic/diseaseManage/disease/Disease'
import AddNewDisease from '@/components/basic/diseaseManage/disease/AddNewDisease'
import EditDiseaseDetail from '@/components/basic/diseaseManage/disease/EditDiseaseDetail'

import DiseasePath from '@/components/basic/diseaseManage/diseasePath/DiseasePath'
import AddNewDisPath from '@/components/basic/diseaseManage/diseasePath/AddNewDisPath'
import EditDisPathDetail from '@/components/basic/diseaseManage/diseasePath/EditDisPathDetail'

import Drug from '@/components/basic/drugManage/drug/Drug'
import AddNewDrug from '@/components/basic/drugManage/drug/AddNewDrug'
import EditDrugDetail from '@/components/basic/drugManage/drug/EditDrugDetail'

import DrugClass from '@/components/basic/drugManage/drugClass/DrugClass'
import AddNewDrugClass from '@/components/basic/drugManage/drugClass/AddNewDrugClass'
import EditDrugClassDetail from '@/components/basic/drugManage/drugClass/EditDrugClassDetail'

import Reference from '@/components/basic/referenceManage/reference/Reference'
import AddNewReference from '@/components/basic/referenceManage/reference/AddNewReference'
import EditRefDetail from '@/components/basic/referenceManage/reference/EditRefDetail'

import Author from '@/components/basic/referenceManage/author/Author'
import AddNewAuthor from '@/components/basic/referenceManage/author/AddNewAuthor'
import EditAuthorDetail from '@/components/basic/referenceManage/author/EditAuthorDetail'

import ChemoTherapy from '@/components/chemo/ChemoTherapy'
import AddNewChemoTherapy from '@/components/chemo/AddNewChemoTherapy'
import EditChemoTherapyDetail from '@/components/chemo/EditChemoTherapyDetail'

import ClinicalTrial from '@/components/target/clinicalTrial/ClinicalTrial'
import AddNewClinicalTrial from '@/components/target/clinicalTrial/AddNewClinicalTrial'
import BrowClinicalTrialDetail from '@/components/target/clinicalTrial/BrowClinicalTrialDetail'
import EditClinicalTrialDetail from '@/components/target/clinicalTrial/EditClinicalTrialDetail'

import TargetedTherapy from '@/components/target/targetedTherapy/TargetedTherapy'
import AddNewTargetedTherapy from '@/components/target/targetedTherapy/AddNewTargetedTherapy'
import EditTargetedTherapyDetail from '@/components/target/targetedTherapy/EditTargetedTherapyDetail'

import TargetedTherapy2 from '@/components/target/targetedTherapy2/TargetedTherapy2'
import AddNewTargetedTherapy2 from '@/components/target/targetedTherapy2/AddNewTargetedTherapy2'
import EditTargetedTherapy2Detail from '@/components/target/targetedTherapy2/EditTargetedTherapy2Detail'

import VariantProfile from '@/components/target/VariantProfile/VariantProfile'
import AddNewVariantProfile from '@/components/target/VariantProfile/AddNewVariantProfile'
import EditVariantProfileDetail from '@/components/target/VariantProfile/EditVariantProfileDetail'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/basicdatamodal',
      component: BasicdataModal,
      children: [
        {
          path: 'chromo',
          component: Chromo
        },
        {
          path: 'addnewchromo',
          component: AddNewChromo
        },
        {
          path: 'browchromodetail',
          component: EditChromoDetail
        },
        {
          path: 'editchromodetail',
          component: EditChromoDetail
        },
        {
          path: 'browchromodetail',
          component: EditChromoDetail
        },
        {
          path: 'gene',
          component: Gene
        },
        {
          path: 'addnewgene',
          component: AddNewGene
        },
        {
          path: 'editgenedetail',
          component: EditGeneDetail
        },
        {
          path: 'browgenedetail',
          component: BrowGeneDetail
        },
        {
          path: 'transcriptlist',
          component: TranscriptList
        },
        {
          path: 'addnewtranscript',
          component: AddNewTranscript
        },
        {
          path: 'browtranscriptdetail',
          component: EditTranscriptDetail
        },
        {
          path: 'edittranscriptdetail',
          component: EditTranscriptDetail
        },
        {
          path: 'snpindel',
          component: SnpIndel
        },
        {
          path: 'addnewsnpindel',
          component: AddNewSNPIndel
        },
        {
          path: 'browsnpindeldetail',
          component: BrowSNPIndelDetail
        },
        {
          path: 'editsnpindeldetail',
          component: EditSNPIndelDetail
        },
        {
          path: 'cnv',
          component: CNV
        },
        {
          path: 'addnewcnv',
          component: AddNewCNV
        },
        {
          path: 'browcnvdetail',
          component: EditCNVDetail
        },
        {
          path: 'editcnvdetail',
          component: EditCNVDetail
        },
        {
          path: 'fusion',
          component: FUSION
        },
        {
          path: 'addnewfusion',
          component: AddNewFusion
        },
        {
          path: 'browfusiondetail',
          component: EditFusionDetail
        },
        {
          path: 'editfusiondetail',
          component: EditFusionDetail
        },
        {
          path: 'disease',
          component: Disease
        },
        {
          path: 'addnewdisease',
          component: AddNewDisease
        },
        {
          path: 'browdiseasedetail',
          component: EditDiseaseDetail
        },
        {
          path: 'editdiseasedetail',
          component: EditDiseaseDetail
        },
        {
          path: 'diseasepath',
          component: DiseasePath
        },
        {
          path: 'addnewdiseasepath',
          component: AddNewDisPath
        },
        {
          path: 'browdiseasepathdetail',
          component: EditDisPathDetail
        },
        {
          path: 'editdiseasepathdetail',
          component: EditDisPathDetail
        },
        {
          path: 'drug',
          component: Drug
        },
        {
          path: 'addnewdrug',
          component: AddNewDrug
        },
        {
          path: 'browdrugdetail',
          component: EditDrugDetail
        },
        {
          path: 'editdrugdetail',
          component: EditDrugDetail
        },
        {
          path: 'drugclass',
          component: DrugClass
        },
        {
          path: 'addnewdrugclass',
          component: AddNewDrugClass
        },
        {
          path: 'browdrugclassdetail',
          component: EditDrugClassDetail
        },
        {
          path: 'editdrugclassdetail',
          component: EditDrugClassDetail
        },
        {
          path: 'reference',
          component: Reference
        },
        {
          path: 'addnewreference',
          component: AddNewReference
        },
        {
          path: 'browrefdetail',
          component: EditRefDetail
        },
        {
          path: 'editrefdetail',
          component: EditRefDetail
        },
        {
          path: 'author',
          component: Author
        },
        {
          path: 'addnewauthor',
          component: AddNewAuthor
        },
        {
          path: 'browauthordetail',
          component: EditAuthorDetail
        },
        {
          path: 'editauthordetail',
          component: EditAuthorDetail
        }
      ]
    },
    {
      path: '/chemotherapymodal',
      component: ChemoTherapyModal,
      children: [
        {
          path: 'chemotherapy',
          component: ChemoTherapy
        },
        {
          path: 'addnewchemotherapy',
          component: AddNewChemoTherapy
        },
        {
          path: 'browchemotherapydetail',
          component: EditChemoTherapyDetail
        },
        {
          path: 'editchemotherapydetail',
          component: EditChemoTherapyDetail
        }
      ]
    },
    {
      path: '/targetedtherapymodal',
      component: TargetedTherapyModal,
      children: [
        {
          path: 'clinicaltrial',
          component: ClinicalTrial
        },
        {
          path: 'addnewClinicalTrial',
          component: AddNewClinicalTrial
        },
        {
          path: 'browclinicaltrialdetail',
          component: BrowClinicalTrialDetail
        },
        {
          path: 'editclinicaltrialdetail',
          component: EditClinicalTrialDetail
        },
        {
          path: 'targetedtherapy',
          component: TargetedTherapy
        },
        {
          path: 'addnewtargetedtherapy',
          component: AddNewTargetedTherapy
        },
        {
          path: 'browtargetedtherapydetail',
          component: EditTargetedTherapyDetail
        },
        {
          path: 'edittargetedtherapydetail',
          component: EditTargetedTherapyDetail
        },
        {
          path: 'targetedtherapy2',
          component: TargetedTherapy2
        },
        {
          path: 'addnewtargetedtherapy2',
          component: AddNewTargetedTherapy2
        },
        {
          path: 'browtargetedtherapy2detail',
          component: EditTargetedTherapy2Detail
        },
        {
          path: 'edittargetedtherapy2detail',
          component: EditTargetedTherapy2Detail
        },
        {
          path: 'variantprofile',
          component: VariantProfile
        },
        {
          path: 'addnewVariantProfile',
          component: AddNewVariantProfile
        },
        {
          path: 'browVariantProfiledetail',
          component: EditVariantProfileDetail
        },
        {
          path: 'editVariantProfiledetail',
          component: EditVariantProfileDetail
        }
      ]
    }
  ]
})



