<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>变异管理</BreadcrumbItem>
      <BreadcrumbItem>CNV</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="染色体编号">
            <Input v-model="searchForm.chromoID" placeholder="请输入染色体编号"></Input>
          </FormItem>
          <FormItem label="起始位置">
            <Input v-model="searchForm.startPosition" placeholder="请输入起始位置"></Input>
          </FormItem>
          <FormItem label="终止位置">
            <Input v-model="searchForm.endPosition" placeholder="请输入终止位置"></Input>
          </FormItem>
          <FormItem label="变异类型">
            <Select v-model="searchForm.variantType" placeholder="请选择变异类型" style="width:162px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="searchForm.clinicalMean" placeholder="请输入临床意义"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" @click="addNewCNV" size="large" icon="android-add">添加新CNV</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalData.length" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        searchForm:{
          chromoID:'',
          startPosition:'',
          endPosition:'',
          variantType:'',
          clinicalMean:''
        },
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"变异编号",
            key:'variantID',
            align: 'center'
          },
          {
            title:"染色体编号",
            key:'chromoID',
            align: 'center'
          },
          {
            title:"起始位置",
            key:'startPosition',
            align: 'center'
          },
          {
            title:"终止位置",
            key:'endPosition',
            align: 'center'
          },
          {
            title:"变异类型",
            key:'variantType',
            align: 'center'
          },
          {
            title:"临床意义",
            key:'clinicalMean',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on:{
                    click:()=>{
                      this.edit(params.index)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            variantID:"11111",
            chromoID:"8",
            startPosition:"1.2.0",
            endPosition:"11111",
            variantType:"2018-03-06",
            clinicalMean:"哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
          },
          {
            variantID:"11111",
            chromoID:"8",
            startPosition:"1.2.0",
            endPosition:"11111",
            variantType:"2018-03-06",
            clinicalMean:"哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
          },
          {
            variantID:"11111",
            chromoID:"8",
            startPosition:"1.2.0",
            endPosition:"11111",
            variantType:"2018-03-06",
            clinicalMean:"哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
          },
          {
            variantID:"11111",
            chromoID:"8",
            startPosition:"1.2.0",
            endPosition:"11111",
            variantType:"2018-03-06",
            clinicalMean:"哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
          }
        ],
        totalData:[],
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('Success!');
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      addNewCNV(){
        this.$router.push("/basicdataModal/addnewcnv")
      },
      show (index) {
        // console.log(index);
        this.$router.push("/basicdatamodal/browcnvdetail")
      },
      edit (index) {
        // console.log(index);
        this.$router.push("/basicdatamodal/editcnvdetail")
      },
      remove(index){
        // console.log(index);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      },
      handlePageSize(val){
        this.pageSize = val;
        // this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


