<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/fusion">FUSION</BreadcrumbItem>
      <BreadcrumbItem>添加新的FUSION变异</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的FUSION变异</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的FUSION变异</p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="90">
          <FormItem label="变异编号">
              <Input v-model="addForm.variantID" placeholder="请输入变异编号" style="width: 280px"></Input>
          </FormItem>
          <div style="overflow:hidden">
            <div class="sortContainer">
              <p>5' Partner Gene</p>
              <FormItem label="Gene Symbol">
                <Input v-model="addForm.fiveGeneSymbol" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="Last Oberved Exon">
                <Input v-model="addForm.lastObervedExon" placeholder="请输入Last Oberved Exon" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="Infered Breakpoint">
                  <Input v-model="addForm.fiveInferedBreakpoint" placeholder="请输入Infered Breakpoint" style="width: 280px"></Input>
              </FormItem>
            </div>
            <div class="sortContainer">
              <p>3' Partner Gene</p>
              <FormItem label="Gene Symbol">
                <Input v-model="addForm.threeGeneSymbol" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="First Oberved Exon">
                <Input v-model="addForm.firstObervedExon" placeholder="请输入First Oberved Exon" style="width: 280px"></Input>
              </FormItem>
              <FormItem label="Infered Breakpoint">
                  <Input v-model="addForm.threeInferedBreakpoint" placeholder="请输入Infered Breakpoint" style="width: 280px"></Input>
              </FormItem>
            </div>
          </div>
          <FormItem label="临床意义">
            <Input v-model="addForm.clinicalMean" type="textarea" :rows="4" placeholder="请输入临床意义" style="width: 723px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          variantID:'',
          fiveGeneSymbol:'',
          lastObervedExon:'',
          fiveInferedBreakpoint:'',
          threeGeneSymbol:'',
          firstObervedExon:'',
          threeInferedBreakpoint:'',
          clinicalMean:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push('/basicdataModal/fusion')
      },
      backList() {
        this.$router.push('/basicdataModal/fusion')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .sortContainer{
      float:left;
      padding: 25px 10px 0;
      background:@bgc;
      margin:0 15px 25px;
      margin-top:0;
      border:1px solid @bgc;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


