<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>转录本详情</BreadcrumbItem>
      <BreadcrumbItem>添加新的转录本</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的转录本</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的转录本</p>
        <Form ref="addForm" :model="addForm" :rules="ruleValidate" label-position="right" :label-width="120">
          <FormItem label="转录本名称" prop="transcriptName">
            <Input v-model="addForm.transcriptName" placeholder="请输入转录本名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="是否设置为当前使用的转录本" prop="isCurrentTrans">
            <RadioGroup v-model="addForm.isCurrentTrans">
              <Radio label="是" value="1"></Radio>
              <Radio label="否" value="0"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="handleSubmit('addForm')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          transcriptName:'',
          isCurrentTrans:''
        },
        ruleValidate: {
          transcriptName: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ],
          isCurrentTrans: [
            { required: true, message: '不能为空', trigger: 'blur' }
          ],
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('Success!');
          } else {
            this.$Message.error('Fail!');
          }
        })
        console.log(this.addForm);
        // this.$router.push('/basicdataModal/chromo')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


