<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/drug">药物信息</BreadcrumbItem>
      <BreadcrumbItem>添加新的药物</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的药物</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的药物</p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="120">
          <FormItem label="药物编号">
              <Input v-model="addForm.drugID" placeholder="请输入药物编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物名称（英文）">
            <Input v-model="addForm.drugNameEN" placeholder="请输入药物名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物名称（中文）">
              <Input v-model="addForm.drugNameCN" placeholder="请输入药物名称（中文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="FDA药物状态">
              <Input v-model="addForm.fdaDrugStatus" placeholder="请输入FDA药物状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="CFDA药物状态">
              <Input v-model="addForm.cfdaDrugStatus" placeholder="请输入CFDA药物状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物类别">
            <Select v-model="addForm.drugCategory" style="width:280px" placeholder="请选择药物类别">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
            <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          drugID:'',
          drugNameEN:'',
          drugNameCN:'',
          fdaDrugStatus:'',
          cfdaDrugStatus:'',
          drugCategory:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push('/basicdataModal/drug')
      },
      backList(name) {
        this.$router.push('/basicdataModal/drug')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


